import React from 'react';
import { Box } from 'grommet';
import * as d3 from 'd3';

import Layout from '../components/layout';

class Index extends React.Component {
  render() {
    let svg = d3.select("svg#AnscombesQuartet");
          let width = svg.attr("width");
          let height = svg.attr("height");
          
          // Using Mike Bostock's convention for margins
          let margin = { "top": 5, "right": 5, "bottom": 15, "left": 25};
          let chartWidth = width - margin.left - margin.right;
          let chartHeight = height - margin.top - margin.bottom;      

          let xScale = d3.scaleLinear()
            .domain([0, 15])
            .range([0, chartWidth]);
          
          let yScale = d3.scaleLinear()
            .domain([0,10])
            .range([chartHeight, 0]);
          
          let leftAxis = d3.axisLeft(yScale)
            .ticks(2)
            .tickSize(0);

          svg.append("g")
            .attr("class", "y axis")
            .attr("transform","translate("+(margin.left)+","+(margin.top)+")")
            .style("font", "8px Arial")
            .call(leftAxis);
          
          let bottomAxis = d3.axisBottom(xScale)
            .ticks(3)
            .tickSize(1);
          
          svg.append("g")
            .attr("class", "x axis")
            .attr("transform","translate("+(margin.left)+","+(chartHeight + margin.top)+")")
            .style("font", "8px Arial")
            .call(bottomAxis);
          
          let AnscombesQuartetData = [[10, 9.14], [8, 8.14], [13, 8.74], [9, 8.77], [11, 9.26],
            [14, 8.1], [6, 6.13], [4, 3.1], [12, 9.13], [7, 7.26], [5, 4.74]];
          
          for(let x = 1; x < 16; x++){
            svg.append("line")
              .attr("x1", 25 + xScale(x))
              .attr("x2", 25 + xScale(x))
              .attr("y1", 5)
              .attr("y2", 185)
              .style("stroke", "black");
          };

          for(let y = 0; y < 10; y++){
            svg.append("line")
              .attr("x1", 25)
              .attr("x2", 295.5)
              .attr("y1", 185 - (yScale(y)))
              .attr("y2", 185 - (yScale(y)))
              .style("stroke", "black");
          };

          AnscombesQuartetData.forEach( (d) => {
            svg.append("circle")
              .attr("r", 2.3)
              .attr("cx", 25 + (xScale(d[0])))
              .attr("cy", 5 + (yScale(d[1])))
              .style("fill", "blue")
              .on("click", function() {
                let circle = d3.select(this);
                circle.style("fill", "red");
              });
            });
    return (
      <Layout>
        <br />
        <Box direction='row' align='center'>
          <svg height={200} width={300} id='AnscombesQuartet'></svg>
          <p>hello</p>
        </Box>
      </Layout>
    );
  }
}

export default Index;
